.profile-container {
    display: flex;
    align-items: center;
    text-align: center;
    justify-content: center;
    height: 10%;
    min-height: fit-content;
}

.profile-parent {
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.profile-details-name {
    font-size: 24px;
    font-family: 'Courier New', Courier, monospace;
}

.profile-details-role {
    display: flex;
    flex-direction: column;
    margin: 14px 0 24px 0;
    max-width: 700px;
}

.profile-details-role h1 {
    font-size: 20px;
    font-family: cursive;
}

.primary-text {
    color: (--white);
}

.highlighted-text {
    color: crimson;
}

.profile-role-tagline {
    font-size: 19px;
    margin: 5px 0 0 0;
    font-family: "Poppins Light";
}

.profile-options .highlighted-btn {
    width: 100%;
    height: 100%;
    border: 2px solid crimson;
    background: crimson;
    color: #fff;
    font-size: 16px;
    font-weight: 500;
    border-radius: 6px;
    cursor: pointer;
    transition: all 0.3s ease;
    height: 47px;
    width: 170px;
}

.profile-options .highlighted-btn:hover {
    margin: 0 0 0 28px;
    background-color: cornsilk;
    color: #111;
}

.profile-picture {
    display: flex;
    align-items: center;
    justify-content: center;
    box-shadow: 0 1px 0px 0px var(--white);
    border-radius: 100%;
    height: 380px;
    width: 380px;
}

.profile-picture-background {
    height: 92%;
    width: 82%;
    background-image: url('../../../assets/Home/profilephoto.jpg');
    background-size: cover;
    border-radius: 50%;
    background-repeat: no-repeat;
    background-position: center;
    transition: all 0.3s ease;

}

.profile-picture-background:hover {
    transform: scale(1.07);
    transition: all 0.3s ease;
}

.colz .colz-icon i {
    color: white;
    align-items: center;
    justify-content: center;
}

.colz .colz-icon i:hover {
    transform: scale(1.5);
}

.fas {
    background-color: crimson;
    padding: 8px 8px 8px 8px;
    margin: 8px 8px 8px 8px;
    align-items: center;
    justify-content: center;
}

.fab {
    background-color: crimson;
    padding: 8px;
    margin: 8px;
    align-items: center;
    justify-content: center;
}

@media screen and (max-width: 1110px) {
    .profile-picture {
        margin: 0 0px 0 80px;
    }

    .profile-details-name {
        font-size: 20px;
    }

    .profile-details-role .primary-text {
        font-size: 12px;
    }

    .profile-options .btn {
        width: 120px;
        padding: 11px 0;
        z-index: 700px;
    }
}

@media screen and (max-width: 910px) {
    .profile-picture {
        height: 320px;
        width: 320px;
        margin: 0 0 0 4px;
    }
}

@media screen and (max-width: 810px) {
    .profile-picture {
        margin: 0 !important;
    }

    .profile-parent {
        flex-direction: column-reverse;
        margin: 40px 0 0 0;
    }

    .profile-options {
        display: flex;
        justify-content: center;
    }

    .profile-details {
        margin: 25px 0 0;
    }

    .profile-options .highlighted-btn {
        margin: 0 !important;
    }

    .profile-options,
    .profile-details-role {
        text-align: center;
    }
}

@media screen and (max-width: 375px) {
    .profile-picture {
        height: 275px;
        width: 275px;
        margin-top: -90px;
    }

    .profile-details {
        width: 90%;
    }
}